@import "../../units";

.header {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 24px;
  margin-bottom: 24px;
  height: 64px;
  box-shadow: 0 1px 0 #e2e6e9;
  // overflow: hidden;

  &__top {
    @extend %flex-centered;
    flex-direction: row;
  }

  &__action {
    @extend %flex-centered;
    max-width: max-content;
    &__counter {
      display: flex;
      justify-content: center;
      padding: 0;
      position: absolute;
      width: 14px;
      height: 14px;
      background-color: $red;
      color: $white;
      border: 1px solid $white;
      border-radius: 50%;
      margin: 18px 0 0 32px;
      font-weight: 600;
      font-size: 9px;
      line-height: 14px;
    }

    & a {
      line-height: 64px;
      width: 64px;
      text-align: center;
      box-shadow: -1px 0 0 #e2e6e9;
    }
  }

  &__link__favorits {
    &--active {
      border-top: 3px solid $white;
      border-bottom: 3px solid $text-primary;
    }

    &:hover {
      border-bottom: 3px solid $text-primary;
      border-top: 3px solid $white;
    }
  }

  &__link__basket {
    &--active {
      border-top: 3px solid $white;
      border-bottom: 3px solid $text-primary;
    }

    &:hover {
      border-top: 3px solid $white;
      border-bottom: 3px solid $text-primary;
    }
  }
}

@media screen and (max-width: 919px) {
  .header__logo {
    visibility: hidden;
  }
}
